btn-global{
    align-items: center;
    background-color: var(--verde);
    border-radius: 10px;
    width: 10rem;
    border-style: none;
    padding: 0.5rem;
    margin: 0.5rem;
    font-family: Creepster;
}

* {
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
  --salmao: #F79767 ;
  --azul: #5ec8f9;
  --verde: #CDDC2D;
  --amarelo: #FFE600;
  --cinza: #1F1D2B;
}

.letra-logo { 
  font-family: Creepster;
  font-weight: normal;
  font-size: 3rem;
  color: var(--amarelo);
  text-shadow: 0px 4px 4px rgb(0 0 0 / 25%);
  text-align: center;
  margin-bottom: 1rem;
}

h2 {
  font-family: Creepster;
  text-align: center;
}

.container {
  background-color: var(--azul);
  height: 100vh;
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
}

.div-login {
  align-items: center;
  background-color: var(--salmao);
  width: 20rem;
  height: 20rem;
  border-radius: 10px;
  padding: 1rem;
}

.form-login button {
  background-color: var(--verde);
  border-radius: 10px;
  width: 5rem;
  border-style: none;
  padding: 0.5rem;
  margin: 0.5rem;
  font-family: Creepster;
  margin-left: 6rem;
}

a {
  text-decoration: none;
  margin-left: 5px;
  font-family: Creepster;
  color: black;
}

a:active,
a:hover {
  text-decoration: underline;
  color: var(--verde);
}

p {
  margin-bottom: 0;
}

.body-default {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}  

img.logo {
  margin: 0;
  align-items: center;
}

label {
  display: flex;
  flex-direction: column;
}

.input {
  border: none;
  background-color: var(--verde);
  margin: 1rem;
  padding: 0.5rem;
  color: #584944;
  border-radius: 5px;
  display: flex;
  width: 15rem;
}

.input::-webkit-input-placeholder {
  color: white;
  font-family: Creepster;
}

.input:-ms-input-placeholder {
  color: white;
  font-family: Creepster;
}

.input::placeholder {
  color: white;
  font-family: Creepster;
}

.message-text {
  text-align: center;
}

.container-logo-btn {
  display: flex;
}

.div-logo {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  justify-items: center;
  margin: 3rem;
}

.footer-login {
  text-align: center;
  font-size: 1rem;
  font-family: 'Barlow Condensed';
}

.form-login {
  align-items: center;
  padding: 0.5rem;
  margin-top: 1.5rem;
}

img.imgRegister {
  width: 20rem;
  height: 20rem;
}
